* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  padding: 0px;
  color: black;
  background-color: white;
  overflow-y: hidden;
}

.popup-content {
  /* margin: auto; */
  background: rgb(255, 255, 255);
  width: 25%;
  padding: 20px;
  top: 200px;
}
.popup-arrow {
  color: rgb(255, 255, 255);
}
[role="tooltip"].popup-content {
  width: 200px;
  box-shadow: hsla(156, 83%, 21%, 0.16) 0px 0px 3px;
}

.popup-overlay {
  /* background: rgba(0, 0, 0, 0.5); */
  background: rgba(52, 64, 84, 0.7);
  backdrop-filter: blur(0px);
}
[data-popup="tooltip"].popup-overlay {
  background: transparent;
}

.edit-content {
  width:600px;
  padding:25px;
  top: 20px;
  border-radius:10px;
  height:420px;
}
